.info {
	&__data {
		margin-top: 36px;
		font-family: 'ProximaNovaBold';
		font-size: 16px;
		color: #1d1d1b;
		line-height: 18px;
		padding-bottom: 10px;
		border-bottom: #ebebeb solid 6px; }
	&__green {
		font-size: 16px;
		line-height: 24px;
		color: #00af42; }
	&__polosa {
		background: #ebebeb;
		height: 6px;
		font-size: 0;
		line-height: 0;
		margin-bottom: 25px; }
	&__text18 {
		font-size: 18px;
		color: #1d1d1b; }
	&__quote {
		padding: 20px 0;
		position: relative;
		font-family: 'ProximaNovaRegIt';
		font-size: 24px;
		line-height: 36px;
		&:before {
			content: '';
			position: absolute;
			left: -40px;
			top: 0;
			@include sprite($quote-icon); }
		.gray {
			font-size: 16px;
			color: #808080; } } }

.page-padding-top-null {
	padding-top: 0; }
