.news {
	@include clearfix;
	&__warp__img {
		height: 192px;
		filter: grayscale(100%);
		img {
			width: 100%; } }
	&__text {
		height: 108px;
		padding: 15px 25px;
		font-size: 16px;
		color: #1d1d1b;
		line-height: 21px; }
	&__date {
		color: #808080;
		display: block;
		padding-bottom: 10px; }
	&__item {
		font-family: 'ProximaNovaReg';
		float: left;
		width: 256px;
		height: 300px;
		border-left: #fff solid 1px;
		border-bottom: #fff solid 1px;
		background: #ebebeb;
		&:hover {
			background: #00af42;
			.news {
				&__text, &__date {
					color: #fff; }
				&__warp__img {
					filter: grayscale(0); } } } } }

