.header {
	position: relative;
	background: #00af42;
	min-height: 128px;
	z-index: 3;
	&:before {
		content: '';
		position: absolute;
		display: block;
		width: 50%;
		left: 0;
		height: 128px;
		background: #ebebeb;
		top: 0;
		bottom: 0; }
	&-bg {
		background: #fff;
		position: relative;
		z-index: 1; }
	&_logotip {
		width: 256px;
		height: 128px;
		float: left;
		z-index: 5;
		background: #ebebeb;
		position: relative;
		&_item {
			position: absolute;
			@include transform_vertical;
			left: 0;
			padding-left: 40px;
			display: block;
			top: 50%;
			&:after {
				margin-top: 5px;
				content: '';
				display: block;
				height: 6px;
				background: #00af42;
				width: 50%;
				@include transition1s; }
			&:hover:after {
				width: 100%; }
			img {
				max-width: none; } } }
	&_right_part {
		margin-left: 256px;
		height: 128px;
		position: relative; }
	&__search {
		float: left;
		margin: 48px 0 39px 40px;
		height: 41px;
		width: 168px;
		padding: 10px 0;
		border-bottom: #ebebeb solid 6px;
		&:hover {
			border-bottom: #d1d1d1 solid 6px; }

		&_big {
			width: 470px; }
		&:before {
			@include sprite($lupa);
			content: '';
			cursor: pointer;
			display: inline-block;
			width: 14px;
			height: 15px;
			margin-right: 9px; }
		&:hover:before {
			@include sprite($lupahover); }
		&__input {
			background: none;
			border: none;
			width: 145px;
			font-size: 16px;
			color: #1d1d1b;
			font-family: 'ProximaNovaBold';
			float: right;
			height: 15px;
			&_big {
				width: 445px; } }
		&__reset {
			width: 15px;
			height: 15px;
			display: none;
			float: right;
			position: absolute;
			right: 0;
			top: 0;
			background: url(../images/sprites.png) no-repeat 0 -29px;
			&:hover {
				@include sprite($close-bt-hover); } }
		&__inner {
			float: right; } }
	&__number {
		position: absolute;
		left: 18px;
		top: -18px;
		height: 18px;
		line-height: 19px;
		font-size: 12px;
		font-family: 'ProximaNovaBold';
		padding: 0 5px;
		background: #c2213a;
		color: #fff;
		@include border-radius(10px); }
	&__phone {
		display: inline-block;
		height: 17px;
		margin-left: 50px;
		text-decoration: none;
		font-family: 'ProximaNovaReg';
		margin-top: 55px;
		width: 93px;
		line-height: 17px;
		color: #808080;
		&:before {
			content: '';
			display: block;
			float: left;
			width: 17px;
			margin-right: 7px;
			height: 17px;
			background: url(../images/sprites.png) no-repeat -16px 0; }
		&:hover {
			color: #d9d9d9; } }
	&__favorit {
		display: inline-block;
		position: relative;
		height: 17px;
		text-decoration: none;
		font-family: 'ProximaNovaReg';
		margin-top: 55px;
		width: 143px;
		line-height: 17px;
		color: #808080;
		&__icon {
			position: relative;
			cursor: pointer;
			z-index: 11;
			&:before {
				content: '';
				display: block;
				float: left;
				width: 17px;
				margin-right: 7px;
				height: 17px;
				background: url(../images/sprites.png) no-repeat -67px 0; }
			.header__number {
				left: 0; } }
		&:hover {
			color: #d9d9d9; } }
	&__basket {
		display: inline-block;
		height: 17px;
		text-decoration: none;
		font-family: 'ProximaNovaReg';
		margin-top: 55px;
		width: 117px;
		line-height: 17px;
		color: #808080;
		height: 70px;
		position: relative;
		z-index: 15;
		&__icon {
			position: relative;
			cursor: pointer;
			z-index: 11;
			&:before {
				content: '';
				display: block;
				float: left;
				width: 17px;
				margin-right: 7px;
				height: 17px;
				background: url(../images/sprites.png) no-repeat -85px 0; }
			.header__number {
				left: 0; } }
		&__inner {
			padding: 20px 50px 0;
			position: absolute;
			top: 88px;
			left: 0;
			right: 0;
			top: 30px;
			left: -502px;
			right: -149px;
			z-index: 15;
			display: none;
			background: #fff; }
		&.hover {
			color: #fff;
			// &:before
			//     content: ''
			//     background: rgba(0, 0, 0, 0.3)
			//     position: fixed
			//     left: 0
			//     right: 0
			//     top: 0
			//     bottom: 0
			//     z-index: 10
			//     display: block
			.header__basket__inner {
				display: block; }
			.header__basket__icon:before {
				position: relative;
				@include sprite($shopping_cart_icon_hover); } } }
	&__enter {
		display: inline-block;
		height: 17px;
		position: relative;
		text-decoration: none;
		font-family: 'ProximaNovaReg';
		margin-top: 55px;
		cursor: pointer;
		width: 105px;
		line-height: 17px;
		color: #808080;
		position: relative;
		&__icon {
			display: inline-block;
			position: relative;
			z-index: 11;
			&:before {
				content: '';
				display: block;
				float: left;
				margin-right: 7px;
				@include sprite($user_icon); } } }
	&__contact {
		display: inline-block;
		height: 17px;
		text-decoration: none;
		font-family: 'ProximaNovaReg';
		margin-top: 55px;
		cursor: pointer;
		width: 125px;
		line-height: 17px;
		color: #808080;
		height: 70px;
		position: relative;
		z-index: 15;
		&__icon {
			position: relative;
			z-index: 11;
			&:before {
				content: '';
				display: block;
				float: left;
				width: 17px;
				margin-right: 7px;
				height: 17px;
				background: url(../images/sprites.png) no-repeat -103px 0; }
			.header__number {
				left: 0; } }
		&.hover {
			color: #fff;
			// &:before
			//     content: ''
			//     background: rgba(0, 0, 0, 0.3)
			//     position: fixed
			//     left: 0
			//     right: 0
			//     top: 0
			//     bottom: 0
			//     z-index: 10
 }			//     display: block
		&__menu {
			position: absolute;
			display: none;
			z-index: 12;
			right: -20px;
			top: 30px;
			width: 300px;
			background: #fff;
			&__item {
				margin-left: 20px;
				margin-right: 20px;
				height: 50px;
				position: relative;
				list-style: none;
				border-bottom: #ebebeb solid 1px;
				&-numder {
					left: auto;
					right: 0;
					top: 50%;
					margin-top: -9px; }
				&-last {
					background: #ebebeb;
					border-bottom: none;
					margin-left: 0;
					margin-right: 0;
					padding-left: 20px;
					padding-right: 20px;
					margin-top: -1px; } }
			&__link {
				height: 50px;
				line-height: 50px;
				font-size: 16px;
				color: #00af42;
				text-decoration: none;
				&:hover {
					color: #00a33f; } } }
		&.hover {
			.header__contact__menu {
				display: block; }
			.header__contact__icon:before {
				position: relative;
				@include sprite($user_icon_hover); } } } }

.hover-bg, .podrobno-bg {
	background: rgba(0, 0, 0, 0.3);
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 10;
	display: none; }

.search {
	&__result {
		display: none;
		padding-bottom: 30px;
		max-height: 430px; }
	&__item {
		height: 85px;
		border-bottom: #f8f8f8 solid 1px;
		&__col-1 {
			height: 84px;
			width: 84px;
			display: block;
			position: relative;
			float: left;
			img {
				@include transform_center; } }
		&__col-2 {
			height: 84px;
			width: 189px;
			font-size: 14px;
			color: #1d1d1b;
			position: relative;
			float: left;
			font-family: 'ProximaNovaReg';
			line-height: 21px;
			span {
				@include transform_vertical;
				a {
					color: #1d1d1b;
					&:hover {
						@include green_color; } } }
			strong {
				font-family: 'ProximaNovaBold';
				@include green_color; } }
		&__col-3 {
			height: 84px;
			width: 127px;
			display: block;
			position: relative;
			float: left;
			font-size: 18px;
			font-family: 'ProximaNovaBold';
			@include green_color;
			div {
				@include transform_vertical; }
			span {
				font-size: 14px;
				color: #b2b2b2; }
			&-4 {
				width: 278px;
				color: #808080;
				font-size: 14px; } }
		&__col-4 {
			height: 84px;
			width: 151px;
			display: block;
			position: relative;
			float: left;
			.jq-number {
				@include transform_vertical; }
			span {
				@include transform_center;
				font-size: 14px;
				font-family: 'ProximaNovaBold';
				color: #231f20;
				display: block; } }
		&__col-5 {
			height: 84px;
			width: 106px;
			display: block;
			position: relative;
			float: left;
			.btn {
				@include transform_vertical; }
			.btn.btn-delete {
				left: auto;
				right: 0;
				margin-right: 10px; } } }
	&__itog {
		display: none;
		@include clearfix;
		padding-bottom: 30px;
		font-size: 14px;
		color: #676767; } }

.basket-small {
	&__itogo {
		height: 40px;
		line-height: 40px;
		font-size: 14px;
		color: #676767; }
	&__sum {
		font-family: 'ProximaNovaBold';
		font-size: 24px;
		color: #00af42;
		padding-left: 10px;
		display: inline-block;
		span, sup {
			font-size: 16px; } } }

.header__search.active {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	background: #fff;
	z-index: 25;
	width: auto;
	height: auto;
	margin: 0;
	padding: 0;
	border: none;
	padding: 48px 50px 0 40px;
	&:before {
		display: none; }
	.header {
		&__search__inner {
			// padding: 48px 50px 0 40px
			padding-right: 15px;
			height: 31px;
			float: none;
			position: relative;
			margin-bottom: 29px;
			border-bottom: #00af42 solid 6px;
			&:before {
				@include sprite($lupa);
				content: '';
				display: inline-block;
				width: 14px;
				height: 15px;
				margin-right: 9px; } }
		&__search {
			&__input {
				background: none;
				border: none;
				width: 640px;
				font-size: 16px;
				float: right;
				height: 15px; }
			&__reset {
				display: block;
				cursor: pointer; } } }
	.search {
		&__result {
			display: block; }
		&__itog {
			display: block; } } }
