@media screen and (max-width: 1023px) {
	.container {
		max-width: none; }
	.header_logotip {
		float: none;
		width: auto; }
	.header_logotip_item {
		padding-left: 0;
		position: relative;
		@include transform_center; }
	.header_right_part {
		margin-left: 0;
		width: auto; }
	.middle-col {
		width: auto;
		left: auto;
		float: none; }
	.left-col {
		float: none;
		right: auto;
		width: auto;
		img {
			display: block;
			margin: auto; } }
	.catalog-active {
		.catalog__inner {
			left: 0;
			top: 64px; } }

	.brendi__li, .brendi__li--big {
		width: 33.3%;
		height: 64px; }
	.brendi__img {
		width: auto;
		max-height: 60px; }
	.brendi:after {
		width: auto;
		height: 0;
		display: block; }
	.news-list-main {
		min-height: 0;
		padding-bottom: 36px; }

	.footer {
		height: auto; }
	.footer_inner {
		padding: 20px; }
	.footer_copyright {
		float: none;
		max-width: none;
		padding-bottom: 20px; }
	.footer_social {
		float: none; }

	.content-text {
		padding-left: 20px;
		padding-right: 20px;
		padding-bottom: 20px; }

	.breadcrams {
		padding-left: 10px; }

	h1 {
		padding-left: 10px; }

	.page-padding, .page-padding-top {
		padding: 15px; }

	.inner-left-col, .inner-right-col {
		width: auto;
		float: none; }

	.form-block {
		padding-left: 15px;
		padding-right: 15px;
		padding-bottom: 15px; }

	.map__contact {
		height: 640px; }

	.faq-block {
		padding: 0 10px; }

	.leftnavmenu {
		min-height: 69px; }

	.leftnavmenu_item {
		float: left; }

	.info__data,
	.info__green,
	.info__text18 {
		padding-left: 20px;
		padding-right: 20px;
		margin-top: 0; }
	.manager {
		padding-bottom: 20px;
		width: 45%;
		float: left; }
	.user-menu {
		float: left;
		width: 45%; }
	.catalog-list__item__col_name_fav {
		width: 260px; }
	.catalog-list__item__col_name_fav ~ .catalog-list__item__col_price {
		width: 105px; } }

@media screen and (max-width: 767px) {
	.header_logotip {
		height: 60px; }
	.header_right_part {
		height: auto; }
	.header__search {
		margin: 20px 10px;
		width: auto;
		float: none; }
	.header__search:before {
		float: left;
		display: block; }
	.header__search__inner {
		float: left;
		width: auto; }
	.header__search__input {
		width: auto;
		float: none; }
	.header__search__input_big {
		width: 300px; }
	.header__phone,
	.header__favorit,
	.header__basket,
	.header__enter,
	.header__contact {
		margin: 20px 0 0;
		padding-left: 10%;
		width: 45%;
		height: 30px; }
	.header__search.active {
		padding-left: 10px;
		padding-right: 10px; }
	.search__item {
		@include clearfix;
		height: auto;
		border-color: #000; }
	.search__item__col-1 {
		width: 20%; }
	.search__item__col-2 {
		width: 80%;
		@include clearfix; }
	.header__basket__inner {
		width: 100vw;
		right: auto;
		left: 0;
		padding-left: 10px;
		padding-right: 10px; }
	.slider-main {
		min-height: 0; }
	.slider-main__content {
		padding: 20px; }
	.slider-main__title {
		font-size: 20px; }
	.catalog {
		height: auto; }
	.catalog.catalog-active {
		padding-left: 0;
		padding-right: 0; }
	.catalog-active .catalog__inner {
		width: auto;
		@include clearfix;
		position: relative;
		left: 0;
		top: 38px; }
	.leftnavmenu {
		padding-left: 5px;
		@include clearfix;
		height: auto; }
	.leftnavmenu_top-grey .leftnavmenu_ul,
	.leftnavmenu_ul {
		padding-top: 20px; }
	.leftnavmenu_top-grey .leftnavmenu_item,
	.leftnavmenu_item {
		margin-bottom: 15px;
		margin-right: 0;
		width: 33%; }
	.brendi__li, .brendi__li--big {
		width: 50%; }
	.about-main__img {
		display: none; }
	.about-main__text {
		padding: 20px; }
	.catalog-list__item {
		@include clearfix;
		height: auto; }
	.catalog-list__item__col_fav {
		width: 10%; }
	.catalog-list__item__col_img {
		width: 20%; }
	.catalog-list__item__col_name {
		width: 70%; }
	.catalog-list__item__col_price {
		margin-left: 30%; }
	.catalog-list__item__col_number-order {
		width: 30%; }
	.item-tovar__haract,
	.item-tovar__price-basket {
		width: auto;
		float: none; }
	.news__item {
		width: 50%;
		height: auto; }
	.news__warp__img,
	.news__text {
		height: auto; }
	.deliver__header__col {
		float: none;
		width: auto; }
	.deliver__header {
		height: auto; }
	.w350 {
		width: auto; }
	.middle-col ol {
		padding-left: 20px; }
	.middle-col ol li:before {
		left: -20px; }
	.find-link {
		float: none; }
	.error-page .left-col {
		display: none;
		height: 0; } }


@media screen and (max-width: 650px) {
	.catalog-list__item__col_price {
		margin-left: 0; }
	.user-menu,
	.manager {
		width: auto;
		float: none;
		padding-left: 10px;
		padding-right: 10px; }
	.error-page__text {
		left: auto;
		right: auto;
		top: auto;
		position: initial;
		transform: none;
		width: auto;
		margin-left: 30%; }
	.error-page .middle-col {
		position: relative;
		background: url(../images/404-bg.jpg) no-repeat left center; }
	.error-page__link {
		position: absolute;
		left: 30%;
		bottom: 20px; } }

@media screen and (max-width: 400px) {
	.header__phone,
	.header__favorit,
	.header__basket,
	.header__contact {
		margin: 10px 0 0;
		padding-left: 10px;
		width: 45%;
		height: 30px; }
	.slider-main {
		display: none; }
	.header__contact__menu {
		width: 100vw;
		left: auto;
		right: -30px; }
	.search__item__col-3,
	.search__item__col-4, {
		width: 50%; }
	.search__item__col-5 {
		width: 100%;
		.btn {
			@include transform_center; } }
	.header__basket__inner {
		.search__item__col-3,
		.search__item__col-4, {
			width: 40%; }
		.search__item__col-5 {
			width: 20%; } }
	.header__search.active {
		padding-top: 20px; }
	.leftnavmenu_top-grey .leftnavmenu_item,
	.leftnavmenu_item {
		width: 50%;
		margin-bottom: 15px; }
	.header__basket {
		z-index: 25; }
	.brendi__li, .brendi__li--big {
		width: 100%; }
	.brendi_about {
		height: 0;
		overflow: hidden;
		padding: 0;
		margin: 0; }
	.about-bg:before {
		display: none; }
	.sort-select,
	.delete-all-tovar-link {
		float: none; }
	.map__contact {
		height: 520px; }
	.contact-block {
		width: auto;
		margin-bottom: 20px;
		margin-top: 200px; }
	.middle-block {
		margin-top: 10px; }
	.item-tovar__haract table th {
		width: 50%; }
	.catalog-list__item__col_img,
	.catalog-list__item__col_price,
	.catalog-list__item__col_number {
		width: 50%;
		margin-left: 0; }
	.catalog-list__item__col_name,
	.catalog-list__item__col_but {
		width: 90%;
		margin-left: 10%;
		.btn {
			@include transform_center; } }

	.catalog-list__item__col_number-order {
		width: 50%; }
	.catalog-list__item__col_but-width {
		clear: both; }
	.catalog-list__item__col_name_fav ~ .catalog-list__item__col_price,
	.catalog-list__item__col_but-width,
	.catalog-list__item__col_but-width ~ .catalog-list__item__col_but {
		width: 50%; }
	.catalog-list__item__col_name_fav ~ .catalog-list__item__col_price {
		margin-left: 10%;
		width: 40%; }
	.news__item {
		width: 100%; }
	.news__warp__img img {
		width: 100%; }
	.deliver__header__col {
		height: auto;
		padding-bottom: 15px; }
	.deliver__row {
		margin: 0 5px;
		padding: 15px 0; }
	.error-page .middle-col {
		background-size: 100% 100%; } }
