.modalbgblock {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: #000;
	opacity: 0.3;
	z-index: 45;
	display: block; }
.modal {
	width: 512px;
	min-height: 200px;
	box-sizing: border-box;
	position: fixed;
	padding: 32px;
	top: 128px;
	display: none;
	z-index: 50;
	left: 50%;
	transform: translate(-50%, 0);
	background-color: #fff;
	.close-modal {
		background-color: #00af42;
		width: 32px;
		height: 32px;
		display: inline-block;
		position: absolute;
		top: 0;
		right: 0;
		&:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			display: inline-block;
			@include transform_center;
			@include sprite($close-modal); } }
	.form-block {
		border-top: none;
		padding: 0;
		&__title {
			font-size: 36px;
			color: #1d1d1b;
			line-height: 40px; } } }
