.footer {
	font-family: 'ProximaNovaLight';
	position: relative;
	height: 128px;
	overflow: hidden;
	&:after {
		content: '';
		position: absolute;
		display: block;
		width: 50%;
		right: 0;
		height: 128px;
		background: #d9d9d9;
		top: 0;
		bottom: 0; }
	&_inner {
		background: #d9d9d9;
		padding: 40px;
		position: relative;
		z-index: 2;
		@include clearfix; }
	&_copyright {
		font-size: 14px;
		color: #808080;
		line-height: 24px;
		max-width: 600px;
		float: left; }
	&_social {
		float: right; } }
a.footer_link {
	font-size: 14px;
	color: #00af42;
	margin-right: 15px;
	height: 17px;
	display: inline-block;
	line-height: 17px;
	text-decoration: none;
	display: inline-block;
	&-fb:before {
		content: '';
		width: 17px;
		height: 17px;
		float: left;
		display: inline-block;
		margin-right: 10px;
		background: url(../images/sprites.png) no-repeat -33px 0; }
	&-in:before {
		content: '';
		width: 17px;
		height: 17px;
		float: left;
		display: inline-block;
		margin-right: 10px;
		background: url(../images/sprites.png) no-repeat -50px 0; }
	&:hover {
		text-decoration: none;
		color: #00a33f; } }
