.error-page {
	height: calc(100vh - 320px);
	background: #00af42;
	.wrapper {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			display: block;
			width: 100%;
			background: #fff;
			left: -100%; } }
	.left-col {
		height: calc(100vh - 320px);
		background: url(../images/errorpage-left.jpg) no-repeat right center #fff; }
	.middle-col {
		height: calc(100vh - 320px);
		background: url(../images/errorpage-right.jpg) no-repeat left center #00af42; }
	&__text {
		position: absolute;
		@include transform_vertical;
		left: auto;
		right: 0;
		width: 330px;
		font-size: 16px;
		line-height: 24px;
		color: #fff; }
	&__link {
		margin-top: 40px;
		font-size: 14px;
		font-family: 'ProximaNovaBold';
		text-transform: uppercase;
		display: block;
		padding-bottom: 6px;
		color: #fff;
		&:after {
			content: '';
			height: 6px;
			background: #fff;
			display: block;
			width: 38px; } } }
