.catalog {
	height: 64px;
	background: #00a33f;
	padding: 26px 28px;
	&__title {
		height: 12px;
		font-size: 14px;
		line-height: 12px;
		color: #ffffff;
		cursor: pointer;
		text-transform: uppercase;
		font-family: 'ProximaNovaBold';
		&:before {
			content: '';
			display: block;
			width: 14px;
			height: 12px;
			float: left;
			margin-right: 8px;
			background: url(../images/sprites.png) no-repeat 0 -17px; }
		&:hover {
			@include green_color;
			z-index: 11;
			position: absolute;
			background: #fff;
			left: 0;
			right: 0;
			top: 0;
			height: 64px;
			padding: 26px 28px;
			bottom: 0;
			display: block;
			&:before {
				background: url(../images/sprites.png) no-repeat -29px -17px; } } }
	&__inner {
		display: none; }
	&-active {
		background: #fff;
		&:before {
			content: '';
			background: rgba(0,0,0,0.3);
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 10;
			display: block; }
		.catalog {
			&__title {
				@include green_color;
				z-index: 11;
				position: absolute;
				background: #fff;
				left: 0;
				right: 0;
				top: 0;
				height: 64px;
				padding: 26px 28px;
				bottom: 0;
				display: block;
				&:before {
					background: url(../images/sprites.png) no-repeat -29px -17px; } }
			&__inner {
				display: block;
				position: absolute;
				left: 100%;
				right: 0;
				top: 0;
				background: #fff;
				z-index: 11;
				width: 768px;
				padding-bottom: 20px; }
			&__ul {
				display: block; }
			&__item {
				padding: 25px 20px 0 30px;
				width: 256px;
				display: block;
				float: left;
				list-style: none;
				&:nth-child(3n+1) {
					clear: both; } }
			&__h4 {
				font-size: 14px;
				@include green_color;
				position: relative;
				cursor: pointer;
				padding-left: 21px;
				font-family: 'ProximaNovaBold';
				&:before {
					position: absolute;
					left: 0;
					top: 0;
					content: '+';
					display: block;
					width: 14px;
					height: 14px;
					background: #00af42;
					line-height: 14px;
					color: #fff;
					text-align: center; }
				&:hover {
					color: #a4a4a4; } }
			&__dropmenu {
				font-size: 14px;
				line-height: 14px;
				padding: 20px 0 0 20px;
				display: none;
				&__item {
					list-style: none;
					display: block;
					padding-bottom: 15px;
					&-last {
						padding-bottom: 5px; } }
				&__link {
					font-size: 14px;
					color: #676767;
					letter-spacing: 0px;
					text-decoration: none;
					&:hover {
						color: #00af42; } }
				&__number {
					@include green_color; } }
			&__more-link {
				text-decoration: none;
				@include green_color;
				&:after {
					content: "\f078";
					font-family: FontAwesome;
					padding-left: 5px;
					display: inline-block; }
				&:hover {
					color: #676767;
					&:after {
						color: #676767; } } }
			&__item.active {
				.catalog {
					&__h4 {
						&:before {
							content: '-'; } }
					&__dropmenu {
						display: block; } } } } } }


.leftnavmenu {
	background: #00af42;
	min-height: 320px;
	padding-left: 28px;
	&_ul {
		padding-top: 30px;
		display: block;
		float: left; }
	&_item {
		font-size: 14px;
		line-height: 14px;
		list-style: none;
		display: block;
		margin-bottom: 20px;
		@include clearfix;
		&:before {
			content: '';
			display: block;
			width: 14px;
			float: left;
			margin-right: 8px;
			height: 12px; }
		&-akcii:before {
			background: url(../images/sprites.png) no-repeat -14px -17px; } }
	&_link {
		font-family: 'ProximaNovaBold';
		font-size: 14px;
		line-height: 14px;
		text-transform: uppercase;
		text-decoration: none;
		color: #ebebeb;
		display: inline-block;
		padding-bottom: 5px;
		&:after {
			margin-top: 5px;
			content: '';
			display: block;
			height: 2px;
			background: #ebebeb;
			width: 0%;
			@include transition1s; }
		&:hover:after {
			width: 100%; } }
	&_top-grey {
		background: #ebebeb;
		min-height: 0;
		height: 64px;
		overflow: hidden;
		padding-left: 40px;
		.leftnavmenu_ul {
			padding-top: 26px; }
		.leftnavmenu_item {
			float: left;
			margin-bottom: 0;
			margin-right: 5px; }
		.leftnavmenu_link {
			@include green_color;
			&:after {
				margin-top: 5px;
				content: '';
				display: block;
				height: 2px;
				background: #00af42;
				width: 0%;
				@include transition1s; }
			&:hover:after {
				width: 100%; } }
		.leftnavmenu_item-akcii:before {
			@include sprite($procent-bg-green); } } }

.slider-main {
	height: 384px;
	position: relative;
	.bx-controls.bx-has-pager {
		position: absolute;
		right: 30px;
		bottom: 25px;
		.bx-pager-item {
			float: left;
			margin-left: 12px;
			height: 14px; }
		.bx-pager-link {
			text-indent: -9999px;
			line-height: 0;
			font-size: 0;
			display: block;
			width: 14px;
			height: 14px;
			border:#cacaca solid 2px {}
			@include border-radius(50%);
			&:hover {
				border-color: #a3a3a3; }
			&.active {
				border-color: #46b571; } } }
	&__item {
		position: relative;
		img {
			width: 100%;
			max-width: none; } }
	&__content {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		font-size: 14px;
		color: #808080;
		padding: 52px 40px; }
	&__title {
		font-family: 'ProximaNovaBold';
		font-size: 36px;
		line-height: normal;
		padding-bottom: 15px;
		color: #1d1d1b; }
	&__link {
		font-family: 'ProximaNovaBold';
		font-size: 14px;
		@include green_color;
		margin-top: 37px;
		text-decoration: none;
		text-transform: uppercase;
		display: inline-block;
		padding: 0 30px;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			margin-top: -1px;
			background: #49bd75;
			height: 2px;
			width: 20px;
			@include transition1s; }
		&:hover {
			text-decoration: none; }
		&:hover:after {
			right: 0;
			width: 100%; } } }

.brendi {
	background: #fff;
	@include clearfix;
	&__bg {
		border-left: #00af42 solid 256px;
		height: 64px;
		display: block;
		background: #fff;
		position: relative;
		z-index: 10; }
	&:after {
		content: '';
		display: block;
		width: 50%;
		height: 64px;
		background: #00af42;
		position: absolute;
		left: 0;
		bottom: 0; }
	&__ul {
		height: 258px;
		overflow: hidden;
		display: block;
		@include clearfix; }
	&__li {
		position: relative;
		list-style: none;
		display: block;
		float: left;
		background: #d9d9d9;
		width: 128px;
		height: 129px;
		// border: #fff solid 1px
		border-top: none;
		border-left: none;
		&:nth-child(2n) {
			background: #fff; }
		&--big {
			width: 256px;
			&:nth-child(odd) {
				background: #ebebeb; } }
		&--small {
			height: 64.5px; } }
	&__link {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		&:hover {
			background: #00af42;
			color: #fff; }
		&--all {
			text-align: center;
			line-height: 64.5px;
			font-family: 'ProximaNovaBold';
			text-transform: uppercase;
			font-size: 14px;
			text-decoration: none;
			color: #fff;
			background: #00af42;
			&:hover {
				background: #00a33f;
				color: #fff; } } }
	&__img {
		position: absolute;
		left: 50%;
		top: 50%;
		display: block;
		@include transform_center; }
	&_about {
		margin-top: 164px;
		padding-bottom: 50px;
		&:after {
			display: none; } }
	&.show_all {
		.brendi {
			&__ul {
				height: auto;
				overflow: visible; } } } }
