.sort-select {
	background: none;
	border: none;
	float: right;
	cursor: pointer;
	font-size: 16px;
	color: #808080;
	padding-right: 16px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: url(../images/sprites/select-pin.png) no-repeat right center;
	position: relative;
	&.active {
		background: url(../images/sprites/select-pin-up.png) no-repeat right center;
		color: #1d1d1b;
		z-index: 20;
		.header__contact__menu {
			display: block;
			.header__contact__menu__item:hover {
				background: #ebebeb;
				margin-left: 0;
				margin-right: 0;
				.header__contact__menu__link {
					margin-left: 20px;
					margin-right: 20px; } } } } }

.delete-all-tovar-link {
	padding-top: 15px;
	color: #808080;
	font-family: 'ProximaNovaReg';
	float: right;
	display: inline-block;
	font-size: 16px;
	line-height: 14px;
	&:before {
		content: '';
		float: left;
		margin-right: 8px;
		display: inline-block;
		@include sprite($delete-icon); }
	&:hover {
		@include green_color;
		&:before {
			@include sprite($close-bt-hover); } } }

.find-link {
	padding-top: 15px;
	color: #808080;
	font-family: 'ProximaNovaReg';
	float: right;
	display: inline-block;
	font-size: 16px;
	line-height: 14px; }


.filter {
	background: #ebebeb;
	padding: 35px 25px;
	&__title {
		font-size: 16px;
		color: #1d1d1b;
		line-height: normal;
		padding-bottom: 15px; }
	&__item {
		padding-bottom: 20px; }
	&__price {
		@include clearfix;
		&__input {
			padding: 0 10px;
			font-size: 16px;
			font-family: 'ProximaNovaReg';
			height: 32px;
			width: 100px;
			display: block;
			float: left;
			background: #ffffff;
			border: #cccccc solid 2px;
			-webkit-appearance: none;
			&::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
				margin: 0; /* в каких-то браузерах присутствует отступ */;
				-webkit-appearance: none; } } }
	&__link-more {
		text-transform: uppercase;
		font-size: 16px;
		font-family: 'ProximaNovaReg'; }
	&__razdeli {
		padding-bottom: 15px;
		&_ul {
			display: block; }
		&_item {
			display: block;
			list-style: none;
			padding-bottom: 13px; }
		&__link {
			font-family: 'ProximaNovaBold';
			font-size: 14px;
			text-transform: uppercase;
			color: #808080;
			position: relative;
			display: inline-block;
			&.active {
				color: #00af42; }
			&:hover {
				color: #00af42; }
			&:after {
				position: absolute;
				left: 0;
				bottom: 0;
				content: '';
				display: inline-block;
				height: 2px;
				background: #00af42;
				width: 0%;
				@include transition1s; }
			&:hover:after {
				width: 100%; } } } }


.popover {
	width: 200px;
	height: 200px;
	border: #f2f2f2 solid 2px;
	z-index: 50;
	img {
		@include transform_center;
		max-width: 200px;
		max-height: 200px; } }

.catalog-list {
	border-top: #ebebeb solid 6px;
	padding-top: 20px;
	padding-bottom: 40px;
	&__title {
		font-size: 18px;
		color: #1d1d1b;
		font-family: 'ProximaNovaBold';
		padding-bottom: 20px; }
	&__item {
		@include clearfix;
		height: 74px;
		border-bottom: #f8f8f8 solid 1px;
		&__col_fav {
			width: 18px;
			height: 73px;
			position: relative;
			float: left; }
		&__col_number-order {
			width: 164px;
			height: 73px;
			position: relative;
			float: left; }
		&__col_img {
			width: 113px;
			height: 73px;
			position: relative;
			float: left; }
		&__col_name {
			width: 310px;
			height: 73px;
			position: relative;
			float: left;
			&_fav {
				width: 515px; }
			&_order {
				width: 215px; } }
		&__col_price {
			width: 118px;
			height: 73px;
			position: relative;
			float: left; }
		&__col_count {
			width: 330px;
			height: 73px;
			position: relative;
			float: left; }
		&__col_number {
			width: 135px;
			height: 73px;
			position: relative;
			float: left;
			.jq-number {
				@include transform_vertical; } }
		&__col_but {
			position: relative;
			float: left;
			height: 73px;
			.btn {
				@include transform_vertical; }
			&-width {
				width: 122px; } } }
	&__number-order {
		@include transform_vertical;
		font-size: 16px;
		color:#808080 {}
		font-family: 'ProximaNovaReg';
		span {
			display: block;
			font-family: 'ProximaNovaBold';
			color: #1d1d1b; } }
	&__fav {
		@include transform_center;
		@include sprite($fav_icon);
		&:hover, &.active {
			@include sprite($fav_icon_hover); } }
	&__img {
		@include transform_center; }
	&__count {
		@include transform_vertical;
		font-family: 'ProximaNovaReg';
		font-size: 16px;
		color: #808080; }
	&__name {
		@include transform_vertical;
		font-family: 'ProximaNovaReg';
		font-size: 16px;
		color: #1d1d1b;
		padding-right: 10px;
		&:hover {
			color: #00af42; } }
	&__price {
		@include transform_vertical;
		font-size: 18px;
		color: #00a33f;
		text-align: left;
		font-family: 'ProximaNovaBold';
		sup {
			font-size: 14px; }
		span {
			font-size: 14px;
			color: #b2b2b2;
			display: block; } }
	&__hit {
		display: inline-block;
		padding: 0 10px;
		background: #00af42;
		height: 18px;
		color: #fff;
		text-align: center;
		font-size: 12px;
		line-height: 19px;
		font-family: 'ProximaNovaBold';
		@include border-radius(10px); }
	&__sale {
		display: inline-block;
		padding: 0 10px;
		background: #c2213a;
		height: 18px;
		color: #fff;
		text-align: center;
		font-size: 12px;
		line-height: 19px;
		font-family: 'ProximaNovaBold';
		@include border-radius(10px); }
	&__order-pay {
		font-size: 16px;
		font-family: 'ProximaNovaBold';
		color: #00af42;
		display: block; }
	&__order-wait-pay {
		font-size: 16px;
		font-family: 'ProximaNovaBold';
		display: block;
		color: #c2213a; }
	&_podrobno {
		padding-top: 0;
		padding-bottom: 0;
		border: none;
		.catalog-list__item__col_fav {
			width: 50px; }
		.catalog-list__item__col_img {
			width: 105px; }
		.catalog-list__item__col_number-order {
			width: 155px; }
		.catalog-list__item__col_name_order,
		.catalog-list__item__col_name {
			width: 200px; }
		.catalog-list__price span {
			display: inline-block; }
		&__first {
			background: #ebebeb;
			height: 89px;
			> div {
				height: 89px; } }
		.catalog-list__item {
			padding-left: 20px; } }
	&__green {
		min-height: 58px;
		background: #00af42;
		padding: 20px;
		position: relative;
		div {
			height: 20px;
			font-size: 16px;
			color: #fff;
			.info {
				@include sprite($attention_icon);
				float: left;
				display: inline-block;
				margin-right: 10px; }
			.close {
				@include sprite($close_icon);
				display: inline-block;
				@include transform_vertical;
				left: auto;
				right: 20px; } } } }


.basket-info {
	font-family: 'ProximaNovaReg';
	text-align: center;
	background: #ebebeb;
	font-size: 16px;
	min-height: 162px;
	padding: 30px 0;
	&-padb {
		padding-bottom: 20px; }
	&__price {
		padding-top: 30px;
		font-family: 'ProximaNovaBold';
		font-size: 32px;
		sup {
			font-size: 20px; }
		span {
			font-size: 20px; }
		small {
			font-size: 16px; }
		&-second {
			margin: 0 20px;
			padding-top: 20px;
			border-top: #e7e7e7 solid 1px; } }
	&__nds {
		font-size: 14px;
		color: #b2b2b2; } }

.fav_del_add-basket {
	display: none;
	position: fixed;
	left: 0;
	right: 0;
	text-align: center;
	height: 64px;
	bottom: 0;
	background: #00af42;
	font-family: 'ProximaNovaBold';
	color: #ffffff;
	font-size: 18px;
	z-index: 50;
	&__text {
		color: #ffffff;
		font-size: 18px;
		line-height: 18px;
		@include transform_center;
		cursor: pointer;
		padding-right: 25px;
		&:after {
			@include sprite($close-modal);
			@include transform_vertical;
			left: auto;
			right: 0;
			content: '';
			display: inline-block; } }
	&.gray {
		background: #ebebeb;
		color: #000;
		.fav_del_add-basket__text {
			color: #000;
			&:after {
				@include sprite($close-modal);
				@include transform_vertical; } } } }
