.faq-block {
	&__item {
		border: #f2f2f2 solid 2px;
		margin-bottom: 24px;
		padding: 15px 20px; }

	&__title {
		font-family: 'ProximaNovaBold';
		color: #1d1d1b;
		font-size: 16px;
		cursor: pointer;
		position: relative;
		padding-right: 10px;
		&:after {
			font-family: FontAwesome;
			content: "\f078";
			@include transform_vertical;
			left: auto;
			right: -5px; } }
	&__answer {
		display: none;
		font-size: 16px;
		line-height: 24px;
		color: #1d1d1b;
		padding-top: 25px;
		h4 {
			font-size: 16px; }
		p {
			padding-bottom: 25px; } }
	&__item.active {
		.faq-block {
			&__answer {
				display: block; } } } }
