.user-menu {
	padding: 35px 28px 0;
	&__ul {
		display: block; }
	&__item {
		padding-bottom: 25px;
		list-style: none; }
	&__link {
		font-family: 'ProximaNovaBold';
		font-size: 14px;
		color: #808080;
		text-transform: uppercase;
		&:hover {
			color: #b2b2b2; }
		&.active {
			color: #00af42; } } }

.manager {
	padding-top: 30px;
	border-top: #dfdfdf solid 2px;
	width: 200px;
	margin: auto;
	text-align: center;
	&__photo {
		width: 150px;
		height: 170px;
		margin: auto;
		img {
			max-width: 100%;
			@include border-radius(50%); } }
	&__name {
		font-size: 14px;
		color: #1d1d1b;
		text-transform: uppercase;
		font-family: 'ProximaNovaBold';
		padding-bottom: 10px;
		text-align: center; }
	&__prof {
		font-size: 14px;
		color: #808080;
		font-family: 'ProximaNovaReg';
		padding-bottom: 20px; }
	&__link {
		font-size: 14px;
		color: #00af42;
		text-transform: uppercase;
		font-family: 'ProximaNovaBold';
		display: block;
		padding-bottom: 15px; } }

.deliver {
	padding-bottom: 50px;
	&__header {
		height: 74px;
		@include clearfix;
		&__col {
			cursor: pointer;
			height: 74px;
			float: left;
			width: 50%;
			padding: 15px 25px 0;
			background: #d9d9d9;
			font-size: 16px;
			color: #1d1d1b;
			position: relative;
			&.active {
				background: #ebebeb;
				&:after {
					content: '';
					@include transform_vertical;
					@include sprite($check_icon);
					left: auto;
					right: 14px; } } } }
	&__content {
		border:#f2f2f2 solid 2px {} }
	&__map {
		height: 420px; }
	&__row {
		margin: 0 20px;
		padding: 30px 0 20px;
		border-bottom: #f8f8f8 solid 1px; } }

.w350 {
	display: inline-block;
	width: 350px; }

.podrobno-info {
	width: 1024px;
	padding: 30px 25px;
	&__title {
		font-size: 36px;
		color: #1d1d1b;
		font-family: 'ProximaNovaBold';
		padding-bottom: 30px; } }
