.checkbox {
	position: relative;
	padding-bottom: 10px;
	input {
		position: absolute;
		left: 0;
		right: 0;
		opacity: 0; }
	label {
		display: inline-block;
		min-height: 16px;
		line-height: 16px;
		font-size: 16px;
		color: #808080;
		cursor: pointer;
		@include clearfix;
		&:before {
			content: '';
			float: left;
			margin-right: 8px;
			margin-top: 1px;
			display: inline-block;
			@include sprite($checkbox); }
		&:hover {
			@include green_color; } }
	input:checked ~ label {
		&:before {
			@include sprite($checkbox-check); } } }

.radio {
	position: relative;
	padding-bottom: 10px;
	input {
		position: absolute;
		left: 0;
		right: 0;
		opacity: 0; }
	label {
		display: inline-block;
		min-height: 16px;
		line-height: 16px;
		font-size: 16px;
		color: #808080;
		cursor: pointer;
		@include clearfix;
		&:before {
			content: '';
			float: left;
			margin-right: 8px;
			margin-top: 1px;
			display: inline-block;
			@include sprite($radio); }
		&:hover {
			@include green_color; } }
	input:checked ~ label {
		color: #1d1d1b;
		&:before {
			@include sprite($radio-active); } } }

.form-block {
	border-top: #ebebeb solid 6px;
	padding-top: 15px;
	padding-bottom: 36px;
	&__title {
		font-family: 'ProximaNovaBold';
		font-size: 18px;
		color: #1d1d1b;
		padding-bottom: 20px; }
	&__row {
		margin-bottom: 24px;
		position: relative;
		label.error {
			@include transform_vertical;
			left: auto;
			font-size: 16px;
			color: #c2213a;
			text-transform: uppercase;
			font-family: 'ProximaNovaBold';
			right: 20px; } }
	&__input {
		font-family: 'ProximaNovaReg';
		height: 50px;
		width: 100%;
		font-size: 16px;
		border: #f2f2f2 solid 2px;
		padding: 0 20px;
		&:active, &:focus {
			color: #1d1d1b;
			border-color: #1d1d1b; }
		&.error {
			border-color: #c2213a;
			color: #c2213a; } }
	&__textares {
		font-family: 'ProximaNovaReg';
		border: #f2f2f2 solid 2px;
		font-size: 16px;
		padding: 15px 20px;
		width: 100%;
		height: 134px; }
	&__submit {
		font-size: 14px;
		font-family: 'ProximaNovaBold';
		color: #fff;
		display: inline-block;
		background: #00a33f;
		border: none;
		width: 256px;
		height: 48px;
		text-align: center;
		text-transform: uppercase;
		cursor: pointer;
		line-height: normal;
		&:hover {
			background: #fff;
			color: #00a33f;
			border: #00a33f solid 2px; }
		&_enter {
			width: 220px; }
		&_ok {
			width: 96px;
			text-transform: uppercase;
			line-height: 48px; }
		&_gray {
			background: #b3b3b3;
			color: #fff; } }
	&__edit {
		@include transform_vertical;
		@include sprite($edit-bg);
		left: auto;
		right: 20px; } }
