.about-bg {
	position: relative;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		display: block;
		width: 100%;
		right: 0;
		height: 488px;
		background: url(../images/about-bg.jpg) no-repeat center top;
		background-size: cover; } }

.content-text {
	font-size: 16px;
	color: #1d1d1b;
