.btn {
	min-width: 105px;
	height: 32px;
	display: inline-block;
	font-family: 'ProximaNovaBold';
	font-size: 14px;
	text-align: center;
	line-height: 28px;
	text-decoration: none;
	color: #00af42;
	border: #00af42 solid 2px;
	&:hover {
		background: #00af42;
		color: #fff; }
	&-green {
		background: #00af42;
		color: #fff;
		&:hover {
			background: #fff;
			color: #00af42;
			border: #00af42 solid 2px; } }
	&-gray {
		background: #ebebeb;
		border-color: #ebebeb;
		color: #1d1d1b;
		&:hover {
			background: #d9d9d9;
			border-color: #d9d9d9; } }
	&-gray-black {
		background: #676767;
		border-color: #676767;
		color: #ffffff;
		&:hover {
			background: #fff;
			color: #676767; } }
	&-delete {
		background: #ebebeb;
		border-color: #ebebeb;
		width: 32px;
		min-width: 32px;
		&:after {
			content: '';
			display: inline-block;
			@include transform_center;
			@include sprite($close-bt); }
		&:hover {
			background: #00af42;
			border-color: #00af42;
			color: #fff;
			&:after {
				@include sprite($close-modal); } } }
	&-basket {
		background: #00af42;
		width: 32px;
		min-width: 32px;
		&:after {
			content: '';
			display: inline-block;
			@include transform_center;
			@include sprite($shopping_cart_icon_hover); }
		&:hover {
			background: #fff;
			border-color: #00af42;
			&:after {
				@include sprite($shopping_cart_icon_green); } }
		&.active {
			background: #ebebeb;
			border-color: #ebebeb;
			width: 32px;
			min-width: 32px;
			&:after {
				content: '';
				display: inline-block;
				@include transform_center;
				@include sprite($close-bt); }
			&:hover {
				background: #00af42;
				border-color: #00af42;
				color: #fff;
				&:after {
					@include sprite($close-modal); } } } }
	&-large {
		height: 40px;
		min-width: 165px;
		line-height: 36px; }
	&-big {
		height: 48px;
		line-height: 48px;
		text-transform: uppercase;
		font-size: 14px; }
	&-full {
		width: 100%; }
	&-white {
		background: #fff;
		border-color: #ebebeb;
		color: #1d1d1b;
		&:hover {
			background: #d9d9d9;
			border-color: #d9d9d9;
			color: #fff; } }
	&-white {
		background: none;
		border-color: #939393;
		color: #1d1d1b;
		&:hover {
			background: none;
			border-color: #1d1d1b;
			color: #939393; } } }
.btn__link {
	font-family: 'ProximaNovaBold';
	font-size: 14px;
	color: #00af42;
	text-decoration: none;
	text-transform: uppercase;
	display: inline-block;
	padding: 0 30px;
	position: relative;
	@include transition1s;
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		margin-top: -1px;
		background: #00af42;
		height: 2px;
		width: 20px;
		@include transition1s; }
	&:hover {
		text-decoration: none; }
	&:hover:after {
		right: 0;
		width: 100%; } }

.btn-lage {
	font-size: 14px;
	font-family: 'ProximaNovaBold';
	color: #00af42;
	border: none;
	width: 220px;
	height: 48px;
	display: inline-block;
	text-align: center;
	text-transform: uppercase;
	cursor: pointer;
	line-height: 48px; }


.jq-number__spin:after {
	content: '';
	@include sprite($plus_icon);
	@include transform_center; }
.jq-number__spin.minus:after {
	content: '';
	@include sprite($minus_icon);
	@include transform_center; }
.jq-number__spin.minus:hover:after {
	@include sprite($minus_icon_hover); }
.jq-number__spin.plus:hover:after {
	@include sprite($plus_icon_hover); }


.grey-bg {
	background: #ebebeb; }
