.zoom-img {
 	border-top: #ebebeb solid 6px;
 	border-bottom: #ebebeb solid 6px;
 	margin-top: 36px;
 	img {
 		display: block;
 		margin: auto; }
 	&__link {
 		position: relative;
 		span {
 			position: absolute;
 			left: 0;
 			bottom: -6px;
 			background: #ebebeb;
 			width: 48px;
 			height: 48px;
 			&:after {
 				content: '';
 				@include sprite($zoom_icon);
 				@include transform_center; } } } }

.item-tovar {
	@include clearfix;
	padding-bottom: 70px;
	&__haract {
		float: left;
		width: 432px;
		table {
			th, td {
				text-align: left;
				font-size: 16px;
				line-height: 24px;
				padding: 13px 0;
				font-family: 'ProximaNovaReg';
				border-bottom: #f8f8f8 solid 1px; }
			th {
				color: #808080;
				width: 270px; }
			td {
				color: #1d1d1b; }
			tr:nth-last-child(1) {
				th, td {
					border: none; } } } }
	&__price-basket {
		float: right;
		width: 256px; }
	&__price {
		font-size: 32px;
		font-family: 'ProximaNovaBold';
		color: #00af42;
		padding-bottom: 18px;
		sup, span {
			font-size: 18px; } }
	&__number {
		padding-bottom: 20px;
		.jq-number {
			height: 48px;
			width: 188px;
			padding-left: 48px;
			padding-right: 48px; }
		.jq-number__spin {
			width: 48px;
			height: 46px; }
		.jq-number__field {
			width: 90px; }
		.jq-number__field input {
			width: 90px;
			height: 46px;
			font-size: 18px; } }
	&__fav {
		float: right;
		display: block;
		width: 48px;
		height: 48px;
		background: #ebebeb;
		position: relative;
		&:after {
			content: '';
			@include sprite($fav_icon);
			@include transform_center; }
		&.active, &:hover {
			&:after {
				@include sprite($fav_icon_hover); } } } }
